<template>
    <div>
        <mt-header title="歌单详情">
            <router-link to="/" slot="left">
                <mt-button icon="back"></mt-button>
            </router-link>
            <mt-button slot="right">分享</mt-button>
        </mt-header>
        <!-- 歌单上传者信息 -->
        <div class="details_man">
            <div class="details_man-l">
                <img v-lazy="playdetails.coverImgUrl" alt="">
            </div>
            <div class="details_man-r">
                <div>
                    <span>{{playdetails.name}}</span>
                </div>
                <div>
                    <span>作者详情页面后期开发</span>
                </div>
            </div>
        </div>
        <!-- 歌单列表 -->
        <div v-for="list in playdetails.tracks.length" :key="list.id">
            <router-link :to="{path:'/songplay',query:{ids:playdetails.tracks[list-1].id,songname:playdetails.tracks[list-1].name}}">
                <div class="details_list">
                    <span>{{list}}.</span>
                    <div class="details_listitem">
                        <span>
                            {{playdetails.tracks[list-1].name}}
                        </span>
                        <span class="color_author">
                            {{playdetails.tracks[list-1].artists[0].name}}--{{playdetails.tracks[list-1].album.name}}
                        </span>
                    </div>
                </div>
            </router-link>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
export default {
  data() {
    return {
      playdetails:'',
      playlist: '',
      tracks: [],
      creator: '',
      avatarUrl: '',
      privileges: []
    }
  },
  created() {
    this._getPlaydetails()
  },
  methods: {
    _getPlaydetails() {
      this.id = this.$route.query.id
        // console.log(this.id)
      axios.get(process.env.API_HOST + '/songList?id=' + this.id)
        .then(res => {
          this.playdetails = res.data.data
        })
    }
  }
}
</script>

<style scoped>
.details_man {
width: 100%;
height: 140px;
display: flex;
align-items: center;
justify-content: space-around;
padding: 20px 0;
color: rgb(255, 255, 255);
background-color: rgb(0, 0, 0);
}
.details_man-l {
width: 45%;
height: 100%;
}
.details_man-l img {
    width: 100%;
    height: 100%;
}
.details_man-r {
    width: 45%;
}
.details_list {
    text-align: left;
    padding: 10px 20px;
    display: flex;
    align-items: center;
    border-bottom: 1px solid slateblue;
    font-size: 1rem;
}
a {
    text-decoration: none;
    color: coral;
}
.details_listitem {
    margin-left: 10px;
    display: flex;
    flex-direction: column;
}
.color_author {
    color: cadetblue;
    font-size: .8rem;
}
</style>


