<template>
    <div class="top">
        <div class="header">
            <router-link to="/">
                <h3>Z.J Music</h3>
            </router-link>
        </div>
        <tab></tab>
    </div>
    
    
</template>

<script>
import Tab from '@/common/Tab/Tab'
export default {
    components: {
        Tab
    }
}
</script>

<style scoped>
.top {
    width: 100%;
    position: fixed;
    top: 0;
    z-index: 5;
}
.header {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 50px;
    width: 100%;
    background-color: slateblue;
}
a {
    text-decoration: none;
}
</style>



