<template>
    <div>
        <mt-header class="mt-header" title="歌手详情">
            <router-link to="" slot="left">
                <mt-button icon="back" @click="$router.back(-1)"></mt-button>
            </router-link>
        </mt-header>
        <div class="singerinfo">
            {{briefDesc}}
        </div>
        <div>
            <router-link class="songslist" :to="{path: '/songplay',query: {ids:songs[item-1].id,songname:songs[item-1].name}}" v-for="item in songs.length" :key="item">
                <div><span>{{item}}.</span></div>
                <div>{{songs[item-1].name}}</div>
            </router-link>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
export default {
    data() {
        return {
            briefDesc: '',
            songs: []
        }
    },
    created() {
        this._getSingerInfo()
    },
    methods: {
     _getSingerInfo() {
         this.id = this.$route.query.id
        // 歌手歌曲
         axios.get(process.env.API_HOST + '/song/artist?id=' + this.id).then(res => {
             this.songs = res.data.data.songs
         }).catch(err => {
             console.log(err)
         })
        //  歌手信息
         axios.get(process.env.API_HOST + '/artist/info?id=' + this.id).then(res => {
            //  console.log(res)
             this.briefDesc = res.data.data.briefDesc
         }).catch(err => {
             console.log(err)
         })
     }   
    }
}
</script>

<style scoped>
.singerinfo {
    width: 100%;
    text-align: left;
    padding: 10px;
    color: rgb(129, 81, 72);
    background-color: cornflowerblue;
}
.songslist {
    display: flex;
    width: 90%;
    margin: 10px auto 10px;
    align-items: center;
    border: 1px solid steelblue;
    padding: 10px;
}
a {
    text-decoration: none;
    color: cornflowerblue;
}
</style>


