<template>
    <div>
        <div class="rank" v-for="item in ranklist.length" :key="item.id">
          <router-link class="ranklist" :to="{path: '/songplay',query:{ids:ranklist[item-1].id,songname:ranklist[item-1].name}}">
            <img v-lazy="ranklist[item-1].picUrl" alt="">
            <div class="ranklist_r">
              <h3>{{ranklist[item-1].name}}</h3>
              <span>{{ranklist[item-1].song.artists[0].name}}</span>
            </div>
          </router-link>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
export default {
  data() {
    return {
      ranklist: []
    }
  },
  created() {
    this._getRankList()
  },
  methods: {
    _getRankList() {
      axios.get(process.env.API_HOST + '/song/newest').then(res => {
        // console.log(res)
        this.ranklist = res.data.data
      }).catch(err => {
        console.log(err)
      })
    }
  }
}
</script>

<style scoped>
.rank {
  width: 100%;
}
.ranklist {
    width: 90%;
    display: flex;
    align-items: center;
    margin: 5px auto 10px;
}
.ranklist_r {
  width: 60%;
  text-align: center;
  color:chocolate;
}
.ranklist img {
    width: 40%;
    border-radius: 15px;
}
.ranklist_r span {
  width: 60%;
  text-align: center;
}
a {
  text-decoration: none;
}
</style>



