import Vue from 'vue'
import Router from 'vue-router'
import Recommend from 'components/Recommend/Recommend'
import Rank from 'components/Rank/Rank'
import Singer from 'components/Singer/Singer'
import SingerInfo from 'components/SingerInfo/SingerInfo'
import Search from 'components/Search/Search'
import Playdetails from 'components/Playdetails/Playdetails'
import Songplay from 'components/Songplay/Songplay'
// import RankList from 'components/RankList/RankList'
import Error from 'common/Error/Error'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/recommend'
    },
    {
      path: '/recommend',
      component: Recommend,
      meta: {
        keepAlive: false
      }
    },
    {
      path: '/rank',
      component: Rank,
      meta: {
        bottom: true,
        keepAlive: false
      }
    },
    {
      path: '/singer',
      component: Singer,
      meta: {
        isplay: true,
        keepAlive: false
      }
    },
    {
      path: '/singerinfo',
      component: SingerInfo,
      meta: {
        tab: true,
        keepAlive: false
      }
    },
    {
      path: '/search',
      component: Search
    },
    {
      path: '/playdetails',
      component: Playdetails,
      meta: {
        tab: true
      }
    },
    {
      path: '/songplay',
      component: Songplay,
      meta: {
      }
    },
    // {
    //   path: '/ranklist',
    //   component: RankList,
    //   meta: {
    //     tab: true
    //   }
    // },
    {
      path: '*',
      component: Error,
      meta: {
        tab: true
      }
    }
  ]
})



