<template>
    <div>
        <span>精品歌单</span>
        <div class="play">
            <div v-for="item in playlist.length" :key="item">
                <router-link class="playlist" :to="{path:'/playdetails',query:{id:playlist[item-1].id}}">
                    <img v-lazy="playlist[item-1].coverImgUrl" alt="">
                    <span>{{playlist[item-1].name}}</span>
                </router-link>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
export default {
  data() {
    return {
      playlist: []
    }
  },
  created() {
    this._getPlaylist()
  },
  methods: {
    _getPlaylist() {
      axios.get(process.env.API_HOST + '/songList/highQuality?cat=全部&pageSize=100').then(res => {
        // console.log(res)
        const playlists = res.data.data
        this.playlist = playlists
      }).catch(err => {
        console.log(err)
      })
    }
  }
}
</script>

<style scoped>
.play {
width: 100%;
}
.playlist {
display: flex;
align-items: center;
text-align: left;
color: rgb(0, 0, 0);
font-size: 1rem;
margin: 5px;
}
.playlist img {
  width: 50%;
}
.playlist span {
  margin: 0 20px;
}
a {
  text-decoration: none;
}
</style>



