<template>
    <div>
        <mt-header class="mt-header" title="歌曲播放">
            <router-link to="" slot="left">
                <mt-button icon="back" @click="$router.back(-1)"></mt-button>
            </router-link>
        </mt-header>
        <audio id="audio" :src="process.env.API_HOST + '/url?id='+ this.id + '&quality=128'" autoplay></audio>
        <div v-if="isLyrics">
            <div @click="toLyrics" class="lyrics">
                <span v-for="item in currentLyric.length" :key="item">
                    {{currentLyric[item-1].txt}}
                </span>
            </div>
        </div>

        <div v-else>
            <div class="top">
                <img v-lazy="process.env.API_HOST + '/pic?id=' + this.id" @click="toLyrics" alt="">
                <span class="songname"><h2>{{songname}}</h2></span>
            </div>

            <!-- 底部 -->
            <div class="bottom">
                <div class="bottom_t">
                    <div>
                        <i class="icon iconfont icon-xin"></i>
                    </div>
                    <div>
                        <i class="icon iconfont icon-xiazai"></i>
                    </div>
                    <div>
                        <i class="icon iconfont icon-pinglun"></i>
                    </div>
                    <div>
                        <i class="icon iconfont icon-gengduo"></i>
                    </div>
                </div>
                <div class="bottom_c">
                    <mt-progress>
                        <div slot="start">{{startnum}}</div>
                        <div slot="end">{{endnum}}</div>
                    </mt-progress>
                </div>
                <!-- 直接相同间距 -->
                <div class="bottom_b">
                    <div>
                        <i class="icon iconfont icon-xunhuan"></i>
                    </div>
                    <div>
                        <i class="icon iconfont icon-prev"></i>
                    </div>
                    <div class="" @click="changeStart">
                        
                        <i style="font-size:2.5rem" v-if="!isPlaying" class="icon iconfont icon-zanting"></i>
                        <i style="font-size:2.5rem" v-else class="icon iconfont icon-bofang"></i>
                    </div>
                    <div>
                        <!-- 下一曲 -->
                        <i class="icon iconfont icon-next"></i>
                    </div>
                    <div>
                        <i class="icon iconfont icon-gengduo-2"></i>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
import Lyric from 'lyric-parser'
export default {
  data() {
    return {
      songname: '',
      isPlaying: false,
      startnum: '00:00',
      endnum: '03:33',
      isLyrics: false,
      currentLyric: []
    }
  },
  created() {
      this._getSongPlay()
  },
  mounted() {

  },
  methods: {
    toLyrics() {
        this.isLyrics = !this.isLyrics
    },
    changeStart() {
        this.isPlaying = !this.isPlaying
        const audio = document.getElementById('audio')
        if(!this.isPlaying) {
            audio.play()
        }else {
            audio.pause()
        }
    },
    _getSongPlay() {
      this.id = this.$route.query.ids
      this.songname = this.$route.query.songname
        // 歌词
      this.$axios.get('/api/lrc?id=' + this.id).then(res => {
          const lyric = res.data
          this.currentLyric = new Lyric(lyric,this.handleLyric).lines
      }).catch(err => {
          console.log(err)
      })
    }
  }
}
</script>

<style scoped>
.top {
    width: 100%;
    height: 240px;
    margin-top: 20px;
}
.top img {
    width: 300px;
    height: 300px;
    border-radius: 25px 25px;
}
.bottom {
    width: 90%;
    margin-left: 5%;
    position: fixed;
    bottom: 0;
}
.bottom_t {
    width: 100%;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.bottom_c {
    width: 90%;
    margin: 0 auto;
}
.bottom_c_b {
    width: 100%;
    display: flex;
    justify-content: space-between;
}
.bottom_b {
    width: 100%;
    height: 150px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
i {
    font-size: 1.5rem;
}
.lyrics {
    width: 100%;
    margin: 20px auto;
    display: flex;
    flex-direction: column;
    color: rgb(34, 165, 158);
    font-size: 1.1rem;
}
.lyrics span {
    margin: 10px;
}
.songname {
    color:darkcyan;
}
</style>


