<template>
    <div>
        <div class="top">
            <input type="text" v-model="inputvalue">
            <mt-button @click="search()">搜索</mt-button>
        </div>
        <div class="searchlist" v-for="item in searchlist.length" :key="item.id">
            <router-link :to="{path: '/songplay',query: {ids:searchlist[item-1].id,songname:searchlist[item-1].name}}">
                <span class="search_color">{{searchlist[item-1].name}}</span>
            </router-link>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
export default {
    data() {
        return {
            inputvalue: '邓紫棋',
            searchlist: []
        }
    },
    created() {

    },
    methods: {
     search() {
         axios.get(process.env.API_HOST + '/search?keyword='+this.inputvalue+'&type=song&pageSize=20&page=0').then(res => {
            //  console.log(res)
             this.searchlist = res.data.data.songs
         }).catch(err => {
             console.log(err)
         })
     }
    }
}
</script>

<style scoped>
.top {
    width: 100%;
    height: 40px;
    margin: 100px auto 20px;
    display: flex;
    justify-content: center;
    flex-direction: row;
    align-items: center;
}
input {
    width: 70%;
    height: 100%;
    text-align: center;
    border: 3px solid sandybrown;
    color:cornflowerblue;
    font-weight: bold;
    font-size: 1.2rem;
}
.searchlist {
    width: 80%;
    line-height: 50px;
    margin: 5px auto;
    border: 1px solid slateblue;
    text-align: left;
    color: rgb(26, 226, 226);
    padding-left: 10px;
}
a {
    text-decoration: none;
}
.search_color {
    color: rgb(189, 19, 211);
}
</style>


