<template>
    <div>
        <mt-swipe class="mt-swipe" :auto="4000">
            <mt-swipe-item v-for="item in bannerlist.length" :key="item">
                <img :src="bannerlist[item-1].picUrl" alt="">
            </mt-swipe-item>
        </mt-swipe>
        <playlist></playlist>
    </div>
</template>

<script>
import axios from 'axios'
import Playlist from 'common/Playlist/Playlist'
export default {
  data() {
    return {
      bannerlist: []
    }
  },
  components: {
    Playlist
  },
  created() {
    this._getBanner()
  },
  methods: {
    _getBanner() {
      axios.get(process.env.API_HOST + '/banner').then(res => {
        // console.log(res)
        this.bannerlist = res.data.data
      }).catch((err) => {
        console.log(err)
      })
    }
  }
}
</script>

<style scoped>
.mt-swipe {
    height: 155px;
    width: 100%;
}
img {
    width: 100%;
}
</style>


