<template>
    <div>
        <span>热门歌手</span>
        <div v-for="item in artists.length" :key="item.id">
            <router-link :to="{path:'/singerinfo',query:{id:artists[item-1].id}}" 
                 class="singerlist">
                <img v-lazy="artists[item-1].picUrl" alt="">
                <span>{{artists[item-1].name}}</span>
            </router-link>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
export default {
    data() {
        return {
            artists: [],
            picUrl: ''
        }
    },
    created() {
        this._getTopSinger()
    },
    methods: {
     _getTopSinger() {
         axios.get(process.env.API_HOST + '/artist/top').then(res => {
            //  console.log(res)
            const artist = res.data.data
             this.artists = artist
         }).catch(err => {
             console.log(err)
         })
     }   
    }
}
</script>

<style scoped>
.singerlist {
    width: 90%;
    margin: 0 auto 10px;
    display: flex;
    align-items: center;
}
.singerlist img {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    margin-right: 10px;
}
a {
    text-decoration: none;
    color: coral;
}
</style>


